.score {
    display: flex;
    gap: 2rem;
    padding: 2rem 0 0 2rem;
}

.button, .button2, .button3 {
  color: white;
  background: none;
  border-radius: 10px;
  /* padding: 1.2em 5em; */
  padding: 1.2em 0;
  width: 10rem;
  letter-spacing: 0.35em;
  font-size: 0.7em;
  transition: background-color 0.3s, box-shadow 0.3s, color 0.3s;
  margin: 1em;
  box-shadow: inset 0 0 0.8em rgba(255, 255, 255, 0.3), 0 0 0.8em rgba(255, 255, 255, 0.3);
  border: #fff solid 2px;
}

.button:hover, .button2:hover, .button3:hover {
  color: rgba(0, 0, 0, 0.8);
  background-color: #fff;
  box-shadow: inset 0 0 0 rgba(255, 255, 255, 0.3), 0 0 1.2em rgba(255, 255, 255, 0.5);
}

.sbutton, .otherButtons {
    padding: 1rem 0 0 2rem;
}

.cardPlace {
    padding: 1rem 2rem 2rem 2rem;
    gap: 0.5rem;
}

.cardContainer {
    background-color: white;
    border-radius: 10px;
}

.cardBox {
    background: linear-gradient(147.66deg, #D79314 4.59%, rgba(215, 147, 20, 0.44) 51.35%, #D79314 100%);
    border-radius: 10px;
}

.bottomPlace {
    padding: 0 2rem;
    color: black;
}

.dCardsPlace, .resPlace {
    padding: 0.5rem 0;
    border-radius: 10px;
    background-color: white;
    color: black;
    height: 7rem;
    font-size: x-large;
}

.dCardsPlace {
    width: 40rem;
}

.resPlace {
    width: 12rem;
}

.dCards1, .dCards2, .dCards3 {
    color: black;
}

#mainBody {
    border: 2px solid black;
}
#cardPlace {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#cardBox {
    padding: 3rem;
}

#dCardsPlace {
    display: flex;
    flex-direction: row;
    justify-content: start;
}

#dCardsBox {
    padding: 3rem;
    margin: 0 3rem 0 3rem;
}

#bottomPlace {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


#resPlace {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#resBox {
    margin: 3rem 0 3rem 0;
    color: black;
    font-size: medium;
}